import React from "react";

import "./modal.css";
import reviewIcon from "../../assets/img/review.svg";
import feedbackIcon from "../../assets/img/feedback.svg";

const Modal = (props) => {
	const { show, onClose, modalType } = props;

	const closeOnEscapeKeyDown = React.useCallback(
		(e) => {
			if ((e.charCode || e.keyCode) === 27) {
				onClose();
			}
		},
		[onClose]
	);

	React.useEffect(() => {
		document.body.addEventListener("keydown", closeOnEscapeKeyDown);
		return function cleanup() {
			document.body.removeEventListener("keydown", closeOnEscapeKeyDown);
		};
	}, [closeOnEscapeKeyDown]);

	if (!show) {
		return null;
	}

	return (
		<div className="modal" onClick={onClose}>
			{modalType === "review" && (
				<div
					className="modal-content"
					onClick={(e) => {
						e.stopPropagation();
					}}
				>
					<div className="modal-header">
						<span className="title">
							Уже были у нас?
							<div className="title-icon">
								<img src={reviewIcon} alt="Оставьте свой отзыв" />
							</div>
						</span>
						<span className="highlighted">Оставьте свой отзыв</span>
					</div>
					<div className="modal-body">
						<div className="d-f">
							<div className="col-50">
								<div className="modal-input">
									<input type="text" placeholder="Введите ваше Имя" />
								</div>
							</div>
							<div className="col-50">
								<div className="modal-input">
									<input type="email" placeholder="E-mail" />
								</div>
							</div>
						</div>
						<div className="d-f">
							<div className="col-100">
								<div className="modal-input">
									<input className="textarea" type="text" placeholder="Текстовое сообщение" />
								</div>
							</div>
						</div>
					</div>
					<div className="modal-footer">
						<button className="button">Отправить</button>
					</div>
				</div>
			)}
			{modalType === "feedback" && (
				<div
					className="modal-content"
					onClick={(e) => {
						e.stopPropagation();
					}}
				>
					<div className="modal-header">
						<span className="title">
							Возникли вопросы?
							<div className="title-icon">
								<img src={feedbackIcon} alt="Cвяжитесь с нами!" />
							</div>
						</span>
						<span className="highlighted">Cвяжитесь с нами!</span>
					</div>
					<div className="modal-body">
						<div className="d-f">
							<div className="col-100">
								<div className="modal-input">
									<input type="text" placeholder="Введите ваше ФИО" />
								</div>
							</div>
						</div>
						<div className="d-f">
							<div className="col-50">
								<div className="modal-input">
									<input type="tel" placeholder="Телефон" />
								</div>
							</div>
							<div className="col-50">
								<div className="modal-input">
									<input type="email" placeholder="E-mail" />
								</div>
							</div>
						</div>
						<div className="d-f">
							<div className="col-100">
								<div className="modal-input">
									<input className="textarea" type="text" placeholder="Текстовое сообщение" />
								</div>
							</div>
						</div>
					</div>
					<div className="modal-footer">
						<button className="button">Отправить</button>
					</div>
				</div>
			)}
		</div>
	);
};

Modal.defaultProps = {
	modalType: "review",
};

export default Modal;
